import React from 'react'
import { Card, CardBody, Heading, Text } from 'uikit'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import { useTranslation } from 'contexts/Localization'
import { getENVChainId } from 'sdk'
import { getGrausAddress, getGrausDecimals } from 'utils/addressHelpers'
import CardValue from './CardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

const GrausStats = () => {
  const { t } = useTranslation()
  
  const chainId = getENVChainId()

  const totalSupply = useTotalSupply(chainId)
 
  const burnedBalance = getBalanceNumber(useBurnedBalance(getGrausAddress(chainId), chainId), getGrausDecimals(chainId))
  
  const grausSupply = totalSupply ? getBalanceNumber(totalSupply, getGrausDecimals(chainId)) - burnedBalance : 0
 
  const initSupplyy = totalSupply ? getBalanceNumber(totalSupply, getGrausDecimals(chainId))  : 0

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading scale="xl" mb="24px">
          {t('tokenStats', { name: "Graus"})}
        </Heading>
        <Row>
          <Text fontSize="14px">{t('totalSupply', { name: "Graus"})}</Text>
          {initSupplyy && <CardValue fontSize="14px" value={initSupplyy} />}
        </Row>
        <Row>
          <Text fontSize="14px">{t('circleAmount', { name: "Graus"})}</Text>
          {grausSupply && <CardValue fontSize="14px" value={grausSupply} />}
        </Row>
        <Row>
          <Text fontSize="14px">{t('totalBurned', { name: "Graus"})}</Text>
          <CardValue fontSize="14px" decimals={0} value={burnedBalance} />
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default GrausStats
