import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { getBep20Contract, getGrausContract } from 'utils/contractHelpers'
import { BIG_ZERO, ChainId } from 'sdk'
import useRefresh from './useRefresh'

const useTokenBalance = (tokenAddress: string, chainId : ChainId) => {
  const [balance, setBalance] = useState(BIG_ZERO)

  const { account } = useWeb3React()

  const { fastRefresh } = useRefresh()

  useEffect(() => {
    const fetchBalance = async () => {
      const contract = getBep20Contract(tokenAddress, chainId)

      const res = await contract.methods.balanceOf(account).call()
      
      setBalance(new BigNumber(res))
    }

    if (account) {
      fetchBalance()
    }
  }, [account, tokenAddress, fastRefresh, chainId])

  return balance
}

export const useTotalSupply = (chainId : ChainId) => {
  const { slowRefresh } = useRefresh()
  const [totalSupply, setTotalSupply] = useState<BigNumber>()

  useEffect(() => {
    async function fetchTotalSupply() {
      const grausContract = getGrausContract(chainId)
 
      const supply = await grausContract.methods.totalSupply().call()

      setTotalSupply(new BigNumber(supply))
    }

    fetchTotalSupply()
  }, [slowRefresh, chainId])

  return totalSupply
}

export const useBurnedBalance = (tokenAddress: string, chainId: ChainId) => {
  const [balance, setBalance] = useState(BIG_ZERO)
  const { slowRefresh } = useRefresh()
 
  useEffect(() => {
    const fetchBalance = async () => {
      const contract = getBep20Contract(tokenAddress, chainId)
 
      const res = await contract.methods.balanceOf('0x000000000000000000000000000000000000dEaD').call()

      setBalance(new BigNumber(res))
    }

    fetchBalance()
  }, [tokenAddress, slowRefresh, chainId])

  return balance
}
 
export default useTokenBalance
